import React from 'react';
import {
  Box, Button, Flex, Heading, Text
} from '@chakra-ui/react';
import LottieAnimation from './LottieAnimation';
import style from './Status.module.css';

type Props = {
    status: 'success' | 'searching' | 'failure';
    title: string;
    description?: string;
    ctaText?: string;
    children?: React.ReactNode;
    footer?: React.ReactNode;
    showFooter?: boolean;
    showCTA?: boolean;
    onNext?: () => void;
}

const Status:React.FC<Props> = ({
  ctaText, title, description, status, children, footer,
  onNext, showFooter = true, showCTA = true
}) => (
  <Flex className={style.container}>
    <Flex className={style.containerInner}>
      <Box w="100%">
        <LottieAnimation name={status} />
        <Heading mt={10} data-testid="status-title" variant="primary">{title}</Heading>
        {description ? (
          <Text data-testid="status-description" variant="regular" marginTop="10px" className="text-light">
            {description}
          </Text>
        ) : null }
        {children}
      </Box>
      {footer
      && <Box>{footer}</Box> }
      {showFooter
        ? (
          <Box className={`default-bg ${style.footerContainer}`}>
            {showCTA
              ? <Button data-testid="status-action" variant="primary" width="100%" onClick={onNext}>{ctaText}</Button> : null }
          </Box>
        ) : null }
    </Flex>
  </Flex>
);

Status.defaultProps = {
  description: undefined
};

export default Status;
