import React, { useEffect, useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import Status from '../../../../components/Status';
import { useAppAsyncDispatch } from '../../../../hooks/store';
import MiniBox from '../../../../layout/MiniBox';
import { getPerson } from '../../../../store/middlewares/user';
import { apiErrorHandler, findNextScreen } from '../../../../utils/general';
import globalStyle from '../../../../Global.module.css';
/**
 * Kyc inreview status view
 *
 * @module KYC
 * @returns {JSX.Element}
 */
const KycReview: React.FC = () => {
  const dispatch = useAppAsyncDispatch();
  const { t } = useTranslation();
  const retryCount = useRef(0);
  const timer = useRef<any>(null);
  const navigate = useNavigate();

  /**
   * fetch person kyc status
   */
  const fetchStatus = useCallback((showLoader = true, autoRefresh = true) => {
    dispatch(getPerson(showLoader))
      .then((person) => {
        if (person.kyc.status !== 'submitted' && person.kyc.status !== 'inReview') {
          findNextScreen(person.kyc).then((s) => {
            navigate(s);
          });
        } else if (autoRefresh) {
          timer.current = setTimeout(() => {
            retryCount.current += 1;
            // hide the loader if retry count is more than 4
            fetchStatus(retryCount.current < 4);
          }, 5000);
        }
      })
      .catch(apiErrorHandler);
  }, [retryCount.current]);

  useEffect(() => {
    fetchStatus();
    return () => {
      clearTimeout(timer.current);
    };
  }, []);

  const onNext = useCallback(() => {
    fetchStatus(true, false);
  }, []);

  return (
    <div className="page">
      <MiniBox varient="box" bodyClass={globalStyle.sucessScreenNoVPadding}>
        <Status status="searching" title={t('kyc_inreview.title')} description={t('kyc_inreview.description')} onNext={onNext} ctaText={t('kyc_inreview.ctaText')} />
      </MiniBox>
    </div>
  );
};

export default KycReview;
