import React, { useEffect, useRef } from 'react';
import lottie from 'lottie-web';
import Success from '../assets/images/animated/success.json';
import Searching from '../assets/images/animated/searching.json';
import Failure from '../assets/images/animated/failure.json';

const nameMap:any = {
  success: Success,
  searching: Searching,
  failure: Failure
};

type Props = {
  name: string;
}

const LottieAnimation = ({ name }: Props) => {
  const animRef = useRef<any>(null);

  useEffect(() => {
    const animation = lottie.loadAnimation({
      container: animRef.current,
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: nameMap[name]
    });

    return () => {
      animation.destroy();
    };
  }, []);

  return (
    <div data-testid="lottie-animation-wrapper" style={{ width: '140px', height: '140px', margin: '0 auto' }} ref={animRef} />
  );
};

export default LottieAnimation;
